
.weather-icon {
  position: fixed;
  top: 10px;
  right: 10px;

  background-color: #ff8383;
  background-blend-mode: exclusion;

  padding: 5px;
  border-radius: 5px;
  max-width: 80px;

  cursor: pointer;

  text-align: center;
}

.weather-icon img {
  background-blend-mode: exclusion;
  scale: 0.1;
}

.weather-icon p {
  font-size: 10px;
  color: white;
  position: relative; 
  font-family: "Roboto", sans-serif;  
  text-align: center; 
  mix-blend-mode: difference;
  user-select: none;
  text-transform: uppercase;
}